import buildDebug from 'debug'

import { pluginUtils, searchUtils } from '@verdaccio/core'
import { Config, Logger, Token, TokenFilter } from '@verdaccio/types'

import ProxyStorageHandler from './storage-handler'
import { getBackend, loadStoragePlugins } from './utils'

export interface StorageConfig {
  // The backend key for handle security and token accesses.
  database_backend: string
  // The backend key for handle search access.
  search_backend: string
  // The backend key for handle packument accesses.
  packument_backend: string
  // The backend key to handle tarball accesses.
  tarball_backend: string
  // The object to hold configurations of backend storages.
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  backends: any
}

const debug = buildDebug('verdaccio:plugin:pro:storage:proxy')

class ProxyStoragePlugin
  extends pluginUtils.Plugin<StorageConfig>
  implements pluginUtils.Storage<StorageConfig> {
  public logger: Logger
  public config: Config
  // The object to hold loaded backend storages.
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  public loadedBackends: any
  private storageConfig: StorageConfig

  public constructor(config: StorageConfig, options: pluginUtils.PluginOptions) {
    debug('start storage proxy')
    super(config, options)
    this.config = options.config
    this.logger = options.logger
    this.storageConfig = config
    // Note: constructor cannot be async, so plugin loading is moved to init
    debug('Verdaccio Pro Storage Proxy plugin is enabled')
  }

  public async init(): Promise<void> {
    debug('init storage plugin')
    this.loadedBackends = await loadStoragePlugins(
      this.storageConfig.backends,
      { ...this },
      // this.config?.serverSettings?.pluginPrefix,
    )

    const db = getBackend(this.loadedBackends, this.storageConfig.database_backend)
    await db.init()
    const search = getBackend(this.loadedBackends, this.storageConfig.search_backend)
    await search.init()
    const packument = getBackend(this.loadedBackends, this.storageConfig.packument_backend)
    await packument.init()
    const tarball = getBackend(this.loadedBackends, this.storageConfig.tarball_backend)
    await tarball.init()
    debug('Verdaccio Pro Storage Proxy plugin initialized')
  }

  // Storage API

  public getPackageStorage(packageName: string): ProxyStorageHandler {
    return new ProxyStorageHandler(this.storageConfig, this.logger, this.loadedBackends, packageName)
  }

  // Secret API

  public async getSecret(): Promise<string> {
    debug('get secret')
    const backend = getBackend(this.loadedBackends, this.storageConfig.database_backend)
    return backend.getSecret()
  }

  public async setSecret(secret: string): Promise<string | null> {
    debug('set secret')
    const backend = getBackend(this.loadedBackends, this.storageConfig.database_backend)
    return backend.setSecret(secret)
  }

  // Package API

  public async get(): Promise<string[]> {
    debug('get complete package list')
    const backend = getBackend(this.loadedBackends, this.storageConfig.database_backend)
    return backend.get()
  }

  public async add(packageName: string): Promise<void> {
    debug('add package %o', packageName)
    const backend = getBackend(this.loadedBackends, this.storageConfig.database_backend)
    return backend.add(packageName)
  }

  public async remove(packageName: string): Promise<void> {
    debug('remove package %o', packageName)
    const backend = getBackend(this.loadedBackends, this.storageConfig.database_backend)
    return backend.remove(packageName)
  }

  public async search(query: searchUtils.SearchQuery): Promise<searchUtils.SearchItem[]> {
    debug('search for %o', query.text)
    const backend = getBackend(this.loadedBackends, this.storageConfig.search_backend)
    return backend.search(query)
  }

  // Token API

  public async readTokens(filter: TokenFilter): Promise<Token[]> {
    debug('get tokens for user %o', filter.user)
    const backend = getBackend(this.loadedBackends, this.storageConfig.database_backend)
    return backend.readTokens(filter)
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  public async saveToken(token: Token): Promise<any> {
    debug('save token for user %o', token.user)
    const backend = getBackend(this.loadedBackends, this.storageConfig.database_backend)
    return backend.saveToken(token)
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  public async deleteToken(user: string, tokenKey: string): Promise<any> {
    debug('delete token for user %o and token %o', user, tokenKey)
    const backend = getBackend(this.loadedBackends, this.storageConfig.database_backend)
    return backend.deleteToken(user, tokenKey)
  }
}

export default ProxyStoragePlugin
