import { pluginUtils, PLUGIN_CATEGORY } from '@verdaccio/core'
import { asyncLoadPlugin } from '@verdaccio/loaders'

import { StorageConfig } from './storage-plugin'

const PLUGIN_PREFIX = 'verdaccio'

export async function loadStoragePlugins(
  backends: unknown,
  options: pluginUtils.PluginOptions,
  pluginPrefix: string = PLUGIN_PREFIX,
) {
  const storeSanitize = (plugin: pluginUtils.Storage<unknown>) => {
    return typeof plugin.getPackageStorage === 'function'
  }

  // console.log('load backends ->', backends)
  return asyncLoadPlugin<pluginUtils.Storage<unknown>>(
    backends,
    options,
    storeSanitize,
    false, // no legacy
    pluginPrefix,
    PLUGIN_CATEGORY.STORAGE,
  )
}

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export function getBackend(backends: any, pluginId: string): pluginUtils.Storage<StorageConfig> {
  console.log('get backends ->', backends)
  const pluginName = `${PLUGIN_PREFIX}-${pluginId}`
  const backend = backends[pluginName]
  if (!backend) {
    throw new Error(`Can not find plugin backend: ${pluginName}`)
  }
  return backend
}
